    font-family: 'Overpass';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local('Overpass Bold'), local('Overpass-Bold'), url(./static/fonts/qFdA35WCmI96Ajtm81keds7N4hoiiVI6.c7f707d.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
html,
body,
#write {
    color: var(--text-color);
    font-size: var(--base-font-size);
    background: #fcfcfc;
    font-family: Overpass, "GlowSansSC", "Helvetica Neue", "pingfang sc", "microsoft yahei", sans-serif;
    font-weight: 400;
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
    /* letter-spacing: -0.5px; */
}
#write h1,
#write h2,
#write h3,
#write h4,
#write h5,
#write h6 {
    color: var(--headings-color);
    font-weight: 700;
    line-height: 1.5;
    margin: 20px 0 15px
}
#write {
    width: 90%;
    margin: 0 auto !important;
    /* max-width: 914px; */
    /* text-align: justify; */
}
#write>h1:first-child {
    margin-top: 1.75rem;
}
#write>h2:first-child {
    margin-top: 1.5rem;
}
#write>h3:first-child {
    margin-top: 1rem;
}
#write>h4:first-child {
    margin-top: 0.5rem;
}
#write h1 {
    font-size: 2.5em
}
#write h2 {
    font-size: 1.75 em
}
#write h3 {
    font-size: 1.45em
}
#write h4 {
    font-size: 1.25em
}
#write h5 {
    font-size: 1.1em
}
#write h6 {
    font-size: 1em;
    font-weight: bold
}
#write p {
    color: var(--text-color);
    line-height: 1.7rem;
    margin: 0 0 8px;
}
#write ul {
    line-height: 1.75rem;
    -webkit-margin-before: 0.6em;
            margin-block-start: 0.6em;
    -webkit-margin-after: 0.6em;
            margin-block-end: 0.6em;
}
#write ol li {
    line-height: 1.75rem;
    -webkit-margin-before: 0.6em;
            margin-block-start: 0.6em;
    -webkit-margin-after: 0.6em;
            margin-block-end: 0.6em;
}
#write u {
    text-decoration: none;
    border-bottom: 1px solid #999;
}
@media screen and (max-width: 48em) {
#write blockquote {
        margin-left: 1rem;
        margin-right: 0;
        padding: 0.5em;
}
}
#write a .md-def-url {
    color: #262a30;
}
#write a {
    color: var(--highlight-color);
    text-decoration: none;
    font-weight: bold;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
}
a:hover {
    text-decoration: underline;
}
#write blockquote {
    border-left: 4px solid #ddd;
    color: #666;
    margin: 0;
    margin-bottom: 10px;
    margin-top: 10px;
    padding: 0 15px
}
#write blockquote p {
    color: #666
}
#write blockquote cite::before {
    content: '-';
    padding: 0 5px
}
#write hr {
    background-image: repeating-linear-gradient(-45deg, #ddd, #ddd 4px, transparent 4px, transparent 8px);
    border: 0;
    height: 3px;
    margin: 40px 0
}
.default-tb {
    border-collapse: collapse;
    border-spacing: 0;
    font-size: 1em;
    margin: 0 0 20px;
    width: 100%;
}
.default-tb tr:nth-child(2n),.default-tb thead {
    background-color: #f9f9f9;
}
.default-tb tbody tr:hover {
    background: #f5f5f5
}
caption,.default-tb td,.default-tb th {
    font-weight: 400;
    padding: 8px;
    text-align: left;
    vertical-align: middle
}
.default-tb tr th {
    border-bottom: 3px solid #ddd;
    font-weight: 700;
    padding-bottom: 10px;
    background-color: var(--bg-color);
}
.default-tb td,.default-tb th {
    border: 1px solid #ddd;
}
.default-tb th {
    font-weight: 700;
    padding-bottom: 10px
}
.default-tb td {
    border-bottom-width: 1px
}
#write img{
    max-width: 100%;
    -webkit-box-shadow: 7px 5px 27px #dadada;
            box-shadow: 7px 5px 27px #dadada;
}
#write pre,#write pre>code{
    font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
    text-align: left 	  !important;
    white-space: pre 	  !important;
    word-spacing: normal  !important;
    word-break: normal    !important;
    word-wrap: normal     !important;
    line-height: 1.4	  !important;
    -moz-tab-size: 8	  !important;
    -o-tab-size: 8		  !important;
    tab-size: 8			  !important;
    -webkit-hyphens: none !important;
    -ms-hyphens: none	  !important;
    hyphens: none		  !important;
}
#write pre>code{
    background: transparent !important;
}
#write code{
    font-family: Menlo,Monaco,Consolas,Courier New,monospace;
    font-size: 1em!important;
    background-color: #f0f0f0;
    border-radius: 3px;
    padding: .2em 0;
}
.hljs-ln-numbers {
    -webkit-touch-callout: none !important;
    -webkit-user-select: none   !important;
    -moz-user-select: none 		!important;
    -ms-user-select: none 		!important;
    user-select: none 			!important;
    text-align: center 			!important;
    color: #ccc 			  !important;
    vertical-align: top 		!important;
    padding-right: 5px 			!important;
}
.hljs-ln-code {
    padding-left: 1em !important;
}
html body .highlight pre, html body #write pre {
    padding: 1em;
    overflow: auto  !important;
    font-size: .85em!important;
    line-height: 1.45;
}

